.App {
  text-align: left;
}
body {

  background-image: url(https://thumbs.dreamstime.com/z/lotus-flower-black-white-isolated-background-312349337.jpg);
  background-repeat:no-repeat ;
  background-position: relative;
  background-size: cover;
  height: 100px;
  width: 100px;
}
.App {
  background-color: #bbc1c1;
  border-radius: 30px;
  padding: 10px 20px;
  transition: transform 0.2s;
  width: 450px;
  text-align: left;
  margin-left: 950px;
  margin-top: 40px;
  font-family:'Courier New', Courier, monospace;
  border:solid 3px rgb(93, 91, 91) ;
 
}

h1 {
  font-size: x-large;
  text-align: center;
  color: #3d1106;
  font-family: cooper;

}

fieldset {
  border: none;
}

input {
  display: block;
  width: 100%;
  margin-bottom: 15px; 
  padding: 5px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 15px;
  font-size: 12px;
}
input[type="radio"],
input[type="checkbox"] {
  display: inline;
  width: 10%;
}


label {
  font-size: 15px;
  display: block;
  width: 100%;
  margin-top: 8px;
  margin-bottom: 5px;
  text-align: left;
  color: #1f1e1e;
  font-weight: bold;
}

button {
  padding: 15px;
  border-radius: 30px;
  margin: 15px;
  border: none;
  color: white;
  cursor: pointer;
  background-color: #21581e;
  width: 40%;
  font-size: 16px;
}




